@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap);
/* Extra Fonts: 'Roboto', 'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue' */
html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  background-color: #7699D4;
  font-family: -apple-system, BlinkMacSystemFont, 'Rubik', sans-serif;
  font-size: larger;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navigation-bar {
    position: fixed;
    width: 100%;
    height: 65px;
    top: 0;
    left: 0;
    color: white;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    align-items: center;
    z-index: 100;
}

.logo {
    color: white;
    text-decoration: none;
    margin-left: 30px;
    text-shadow: 1px 1px 5px #000000;
}
.logo:hover {
    color: #5887FF;
    text-shadow: none;
}

.mobile-space {
    flex: 1 1;
}

.space {
    flex: 1 1;
}

.navbar-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.navbar-items li {
    padding: 0 20px;    /* adds space between navbar links */
}

/* when NavLink isn't clicked */
.navbar-items a {
    color: rgb(187, 185, 185);
    text-decoration: none;
    text-shadow: 1px 1px 5px #000000;
}

.navbar-items a:hover {
    color: white;
}

/* when display screen is less than 512 pixels wide, then align logo (name) to left */
@media (max-width: 512px) {
    .mobile-space,
    .space {
        display: none;
    }
}

/* when display screen is between 512 and 768 pixels wide, then center logo (name) */
@media (max-width: 768px) {
    .logo {
        flex: 1 1;
        margin-left: 0px;
    }
    .navbar-items {
        display: none;
    }
}

/* when display screen is larger or equal to 769 pixels wide, don't display menu button */
@media (min-width: 769px) {
    .menu-button,
    .mobile-space {
        display: none;
    }
}
.toggle-button {
    display: flex;
    flex-direction: column;
    height: 20px;
    width: 30px;
    justify-content: space-between;
    background: transparent;
    border: none;
    cursor: pointer;
    box-sizing: border-box; /* keeps button lines inside toggle button's width and height restraints */
    margin-left: 20px;
}

.toggle-button:focus {
    outline: none;
}

.button-line {
    width: 25px;    /* 100% doesn't work because button-line are divs inside toggle-button div */
    height: 2px;
    background: white;
    box-shadow: 1px 1px 3px #000000;
}

@media (max-width: 512px) {
    .toggle-button {
        margin-right: 20px;
    }
}
.drawer {
    width: 40%;
    max-width: 300px;
    height: 100%;
    background: rgba(26, 25, 25, 0.95);
    box-shadow: 13px 0px 25px -2px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 102;
    padding: 15px 10% 0px 15px;
    transform: translateX(-110%);   /* drawer is positioned all the way to left / not on screen */
    transition: transform 0.3s ease-out;  /* animates drawer when tranform property changes, animates for 0.3 seconds, ease-out = start animation fast and end it slowly */
}

/* for elements with both 'drawer' and 'open' classes ... */
.drawer.open {
    transform: translateX(0);   /* default position */
}

.drawer ul {
    width: 90%;
    /* background-color: teal; */
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */}

.drawer a {
    width: 100%;
    color: rgba(255, 255, 255, 0.31);
    /* background-color: tomato; */
    text-decoration: none;
    font-size: 25px;
    /* make name and icon of page align on the center axis */
    display: flex;
    align-items: center;
    margin: 10% 0% 10% 0%;
}

.drawer a:hover {
    color: white;
}

@media (min-width: 769px) {
    .drawer {
        display: none;
    }
}

@media (max-width: 425px) {
    .drawer a {
        margin: 10% 0% 10% -15%;
    }
}

@media (max-width: 375px) {
    .drawer a {
        margin: 10% 0% 10% -20%;
    }
}

@media (max-width: 320px) {
    .drawer a {
        margin: 10% 0% 10% -30%;
    }
}
.close-button {
    background: transparent;
    /* padding: 0px 0px 0px 5px; */
    border: none;
    cursor: pointer;
}

.close-button:hover > * {
    color: white !important;
}

.close-button:focus {
    outline: none;  /* remove outline of button when clicked */
}
.backdrop {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    z-index: 101;   /* lower than z-index of MenuDrawer so that it sits underneath MenuDrawer and over everything else */
}

/* comment this out when you want to use just the backdrop (e.g. displaying modals) and not the drawer */
@media (min-width: 769px) {
    .backdrop {
        display: none;
    }
}
.home {
    height: 100%;
