/* base of cloud */
.cloud {
    width: 300px;
    height: 100px;
    border-radius: 50px;
    position: relative;
    background: #fff;
    box-shadow: 0 8px 5px rgba(0, 0, 0, 0.05);
    opacity: 50%;
    z-index: 0;     /* by default when transform is used */
}
/* left clump of cloud */
.cloud:before {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: absolute;
    top: -50px;
    left: 45px;
    background: #fff;
    content: '';
}
/* right clump of cloud */
.cloud:after {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    position: absolute;
    top: -75px;
    right: 40px;
    background: #fff;
    content: '';
}
/* cloud animations */
.cloud1 {
    transform: scale(0.5);
    opacity: 25%;
    -webkit-animation: animateCloud1Initial 30s 1, animateCloud1Loop 60s linear infinite 30s;
            animation: animateCloud1Initial 30s 1, animateCloud1Loop 60s linear infinite 30s;
}
@-webkit-keyframes animateCloud1Initial {
    0% {left: 60%}
    100% {left: 100%}
}
@keyframes animateCloud1Initial {
    0% {left: 60%}
    100% {left: 100%}
}
@-webkit-keyframes animateCloud1Loop {
    0% {left: -30%}
    100% {left: 100%}
}
@keyframes animateCloud1Loop {
    0% {left: -30%}
    100% {left: 100%}
}
.cloud2 {
    transform: scale(0.75);
    opacity: 38%;
    -webkit-animation: animateCloud2 70s linear infinite;
            animation: animateCloud2 70s linear infinite;
}
@-webkit-keyframes animateCloud2 {
    0% {margin-left: -50%;}
    100% {margin-left: 100%;}
}
@keyframes animateCloud2 {
    0% {margin-left: -50%;}
    100% {margin-left: 100%;}
}
.cloud3 {
    -webkit-animation: animateCloud3Initial 50s linear 1, animateCloud3Loop 65s linear infinite 45s;
            animation: animateCloud3Initial 50s linear 1, animateCloud3Loop 65s linear infinite 45s;
}
@-webkit-keyframes animateCloud3Initial {
    0% {margin-left: 10%;}
    100% {margin-left: 150%;}
}
@keyframes animateCloud3Initial {
    0% {margin-left: 10%;}
    100% {margin-left: 150%;}
}
@-webkit-keyframes animateCloud3Loop {
    0% {margin-left: -100%;}
    100% {margin-left: 150%;}
}
@keyframes animateCloud3Loop {
    0% {margin-left: -100%;}
    100% {margin-left: 150%;}
}

