    }
}
@keyframes animateWave {
    0%, 100% {
        -webkit-clip-path: polygon(0 76%, 13% 74%, 29% 75%, 44% 80%, 57% 86%, 71% 87%, 85% 87%, 100% 82%, 100% 100%, 0% 100%);
